import { createStore } from 'vuex'

export default createStore({
  state: {
    projects:[]
  },
  mutations: {
    initProjectList(state, projects){
      state.projects = projects;
    }
  },

  getters:{
    allProjects: (state, getters) => {
      return state.projects;
    }
  },

  actions: {
  },
  modules: {
  }
})
