    const center =  ref([11.5, 104.9]);
    const geojson = ref(store.getters.allProjects);

    return {
      zoom,
      center,
      bounds: bounds,
      mapOptions: {
        zoomControl: true,
        attributionControl: true,
        zoomSnap: true,
      },
      tileProviders: ref(basemaps),
      polylines: polylines,
      markers:markers,
      poly1: poly1,
      poly2: poly2,
      geojson: geojson
      // router1: router
    }
  },

  data() {
    return{
      fillColor: "#e4ce7f",
      marker: latLng(11.5, 104.9)
    }
  },
  computed: {
    options() {
      return {
        onEachFeature: this.onEachFeatureFunction,
        style: function(feature) {
          if (feature.properties.in_port) {
            return {
              weight: 2,
              color: '#00FF00'
            }
          } else {
            return {
              weight: 2,
              color: '#FF0000'
            }
          }
        }
      };
    },
    styleFunction() {
      const fillColor = this.fillColor; // important! need touch fillColor in computed for re-calculate when change fillColor
      return () => {
        return {
          weight: 2,
          color: "#ECEFF1",
          opacity: 1,
          fillColor: fillColor,
          fillOpacity: 1
        };
      };
    },
    onEachFeatureFunction() {
      
      return (feature, layer) => {
        layer.bindPopup(
          "<div>code:" +
            feature.properties.id +
            "</div><div>owner_kh: " +
            feature.properties.owner_kh +
            "</div>",
          { permanent: false, sticky: true }
        );
      };
    }
  },
  methods: {

  }
